import React, { 
    Fragment, useState, 
    useEffect, useContext
} from "react";

import {
    Grid, useTheme,
    Button, Typography, Card,
    CardContent, CardActions,
    List, ListItem, 
    ListItemText, Divider,
    Accordion, AccordionDetails,
    AccordionSummary, Menu,
    TextField, MenuItem
} from "@material-ui/core";

import { useSnackbar } from "notistack";
import {
    Add, ExpandMore, Folder,
    PlayCircleFilled
} from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";
import GeneralStyles from "../../GeneralStyles";
import PageTitle from "../reusables/PageTitle";
import RainfallForm from "./RainfallForm";
import SurficialForm from "./SurficialForm";
import EarthquakeForm from "./EarthquakeForm";
import SubsurfaceForm from "./SubsurfaceForm";
import MomsForm from "./MomsForm";
import OnDemandForm from "./OnDemandForm";
import { getScenarioGroups, updateScenarioGroupData, getAllSensorsData } from "./ajax";
import StartScenarioModal from "./StartScenarioModal";
import { capitalizeFirstLetter } from "../../UtilityFunctions";
import { GeneralContext } from "../contexts/GeneralContext";

const useStyles = makeStyles(theme => {
    const gen_style = GeneralStyles(theme);
    
    return {
        ...gen_style,
        tabBar: {
            ...gen_style.pageContentMargin,
            margin: 0
        },
        tabBarContent: {
            marginTop: 16
        },
        sticky: {
            position: "sticky",
            top: 146,
            [theme.breakpoints.down("sm")]: {
                top: 48
            },
            backgroundColor: "white",
            zIndex: 1
        },
        noFlexGrow: { flexGrow: 0 },
        paper: {
            position: "fixed",
            right: 0,
            top: 116,
            width: 400
        },
        overflow: {
            overflowY: "auto",
            height: "calc(100vh - 250px)",
            [theme.breakpoints.down("md")]: {
                height: "80vh"
            }
        },
        insetDivider: { padding: "8px 70px !important" },
        nested: { paddingLeft: theme.spacing(4) },
        hidden: { display: "none !important" },
        form_message_style: {
            color: "red",
            fontStyle: "italic"
        },
        listMaxWidth: {
            width: "100%"
        }
    };
});

function defaultValues (type) {
    let default_object;

    if (type === "rainfall") {
        default_object = {
            site_id: 0,
            cumulative: 0,
            rain_alert: "a",
            threshold: 0,
            rain_id: 0,
            source_id: 3,
            ts: ""
        };
    } else if (type === "surficial") {
        default_object = {
            site_id: 2,
            weather: "MAARAW",
            meas_type: "EVENT",
            alert_level: 0,
            source_id: 2,
            ts: ""
        };
    } else if (type === "subsurface") {
        default_object = {
            site_id: 0,
            alert_level: 0,
            disp_alert: 0,
            na_status: 1,
            node_id: 0,
            tsm_id: 0,
            vel_alert: 0,
            source_id: 1,
            ts: ""
        };
    } else if (type === "earthquake") {
        default_object = {
            site_id: 0,
            magnitude: 0,
            depth: 0,
            latitude: 0,
            longitude: 0,
            critical_distance: 0,
            issuer: "SOFTWAREINFRATOPS",
            processed_value: 1,
            distance: 0,
            source_id: 4,
            ts: ""
        };
    } else if (type === "on_demand") {
        default_object = {
            site_id: 1,
            source_id: 5,
            tech_info: "",
            reason: "",
            reporter_id: 2,
            alert_level: "1",
            ts: ""
        };
    } else if (type === "moms") {
        default_object = null;
    }
        
      


    return default_object;

}

function ScenarioList (props) {
    const { selectedScenarioData: { scenario_data }, 
        setSelectedScenarioData,
        sourceIds,
        deployedNode,
        rainfallGauges,
        rainfallPriorities,
        tsmSensors,
        selectedGroupIndexes,
        setSelectedGroupIndexes,
        scenarioGroups,
        setScenarioGroups,
        addedForm,
        onScenarioClick
    } = props;
    const classes = useStyles();
    const { sites } = useContext(GeneralContext);

    // eslint-disable-next-line consistent-return
    const AlertDetails = (row, scenario_data_index) => {
        const { type, type_data } = row;
        if (type === "rainfall") {
            return (
                <RainfallForm 
                    data={type_data} 
                    sites={sites} 
                    sourceIds={sourceIds}
                    rainfallGauges={rainfallGauges}
                    rainfallPriorities={rainfallPriorities}
                    data_index={scenario_data_index}
                    selectedGroupIndexes={selectedGroupIndexes}
                    setSelectedGroupIndexes={setSelectedGroupIndexes}
                    setSelectedScenarioData={setSelectedScenarioData}
                    scenarioGroups={scenarioGroups}
                    setScenarioGroups={setScenarioGroups}
                    onScenarioClick={onScenarioClick}
                />
            );
        } if (type === "earthquake") {
            return (
                <EarthquakeForm 
                    data={type_data} 
                    sites={sites} 
                    sourceIds={sourceIds} 
                    data_index={scenario_data_index}
                    selectedGroupIndexes={selectedGroupIndexes}
                    setSelectedGroupIndexes={setSelectedGroupIndexes}
                    setSelectedScenarioData={setSelectedScenarioData}
                    scenarioGroups={scenarioGroups}
                    setScenarioGroups={setScenarioGroups}
                    onScenarioClick={onScenarioClick}
                />
            );
        } if (type === "surficial") {
            return (
                <SurficialForm 
                    data={type_data} 
                    sites={sites} 
                    sourceIds={sourceIds} 
                    data_index={scenario_data_index}
                    selectedGroupIndexes={selectedGroupIndexes}
                    setSelectedGroupIndexes={setSelectedGroupIndexes}
                    setSelectedScenarioData={setSelectedScenarioData}
                    scenarioGroups={scenarioGroups}
                    setScenarioGroups={setScenarioGroups}
                    onScenarioClick={onScenarioClick}
                />
            );
        } if (type === "moms") {
            const { site } = row;
            return (
                <MomsForm 
                    data={type_data} 
                    sites={sites}
                    site={site}
                    sourceIds={sourceIds} 
                    data_index={scenario_data_index}
                    selectedGroupIndexes={selectedGroupIndexes}
                    setSelectedGroupIndexes={setSelectedGroupIndexes}
                    setSelectedScenarioData={setSelectedScenarioData}
                    scenarioGroups={scenarioGroups}
                    setScenarioGroups={setScenarioGroups}
                    onScenarioClick={onScenarioClick}
                />
            );
        } if (type === "subsurface") {
            return (
                <SubsurfaceForm 
                    data={type_data} 
                    sites={sites} 
                    sourceIds={sourceIds} 
                    data_index={scenario_data_index}
                    deployedNode={deployedNode}
                    tsmSensors={tsmSensors}
                    selectedGroupIndexes={selectedGroupIndexes}
                    setSelectedGroupIndexes={setSelectedGroupIndexes}
                    setSelectedScenarioData={setSelectedScenarioData}
                    scenarioGroups={scenarioGroups}
                    setScenarioGroups={setScenarioGroups}
                    onScenarioClick={onScenarioClick}
                />
            );
        } if (type === "on_demand") {
            return (
                <OnDemandForm 
                    data={type_data} 
                    sites={sites} 
                    sourceIds={sourceIds} 
                    data_index={scenario_data_index}
                    selectedGroupIndexes={selectedGroupIndexes}
                    setSelectedGroupIndexes={setSelectedGroupIndexes}
                    setSelectedScenarioData={setSelectedScenarioData}
                    scenarioGroups={scenarioGroups}
                    setScenarioGroups={setScenarioGroups}
                    onScenarioClick={onScenarioClick}
                />

            );
        }
        
    };

    return (
        <Grid container style={{ margin: "10px" }}>
            {
                // eslint-disable-next-line no-nested-ternary
                scenario_data.length > 0 ? (
                    scenario_data.map((row, index) => {
                        const { type } = row;
                        return (
                            <Grid item xs={12} key={`scenario_data_list_${index}`} style={{ paddingTop: "5px" }}>
                                <Card className={classes.listMaxWidth}>
                                    <CardContent>
                                        <Typography variant="subtitle1" display="block">
                                            {capitalizeFirstLetter(type.replace("_", " "))}
                                        </Typography>
                                        {AlertDetails(row, index)}
                                    </CardContent>
                                </Card>
                            </Grid>
                        );
                    })
                ) : (
                    addedForm.length === 0 && (
                        <Grid item xs={12}>
                            <Typography variant="subtitle1" display="block" >
                                No existing data. Please press <strong>add alert</strong> button to add.
                            </Typography>
                        </Grid>
                    )
                )
            }
            
        </Grid>
    );
}

function Container (props) {
    const classes = useStyles();
    const theme = useTheme();

    const default_menu = [
        "rainfall", "subsurface", "surficial",
        "earthquake", "on_demand", "moms"
    ];
    
    const source_ids = [
        { source_id: 1, trigger_source: "subsurface" },
        { source_id: 2, trigger_source: "surficial" },
        { source_id: 3, trigger_source: "rainfall" },
        { source_id: 4, trigger_source: "earthquake" },
        { source_id: 5, trigger_source: "on demand" },
        { source_id: 6, trigger_source: "moms" },
    ];
    const { sites } = useContext(GeneralContext);
    const [expanded, setExpanded] = useState(false);
    const [selectedIndex, setSelectedIndex] = useState(null);
    const [showAddFolderInput, setShowAddFolderInput] = useState(false);
    const [anchorEl, setAnchorEl] = useState(null);
    const [isAddNewScenario, setIsAddNewScenario] = useState(false);
    const [selected_group, setSelectedGroup] = useState(null);

    const [scenario_groups, setScenarioGroups] = useState([]);
    const [selected_scenario, setSelectedScenario] = useState(null);
    const [selected_scenario_data, setSelectedScenarioData] = useState(null);
    const [selected_group_indexes, setSelectedGroupIndexes] = useState(null);
    const [folder_name, setFolderName] = useState("");
    const [scenario_name, setScenarioName] = useState("");
    const [menu, setMenu] = useState([]);
    const [added_form, setAddedForm] = useState([]);

    const [save_folder_button_state, setSaveFolderButtonState] = useState(true);
    const [save_new_scenario_button_state, setSaveNewScenarioButtonState] = useState(true);

    const [deployed_node_data, setDeployedNodeData] = useState([]);
    const [tsm_sensors_data, setTsmSensorsData] = useState([]);
    const [rainfall_priorities, setRainfallPrioritiesData] = useState([]);
    const [rainfall_gauges, setRainfallGaugesData] = useState([]);

    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const [start_scenario_modal_state, setStartScenarioModalState] = useState(false);
    

    const snackBarActionFn = key => {
        return (<Button
            color="primary"
            onClick={() => { closeSnackbar(key); }}
        >
            Dismiss
        </Button>);
    };

    const onScenarioClick = (group_index, group_data_index, row) => {
        const { scenario_name: name } = row;
        setAddedForm([]);
        const temp_selected_scenario = scenario_groups[group_index].data[group_data_index];
        setSelectedScenarioData(temp_selected_scenario);
        setSelectedIndex(`${group_index}_${group_data_index}`);
        setSelectedScenario(name);
        setIsAddNewScenario(false);
        setSelectedGroupIndexes({
            group_index, group_data_index
        });
        
        const temp = temp_selected_scenario.scenario_data;
        const current_alert_type = temp.map(data => data.type);
        const menu_list = default_menu.filter(val => !current_alert_type.includes(val));
        
        if (!menu_list.includes("moms")) {
            menu_list.push("moms");
        }
        setMenu(menu_list);
        setShowAddFolderInput(false);
        
    };

    const handleChange = (panel, group_index) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
        setSelectedGroup(group_index);
    };

    const showAddFolderInputField = () => {
        setShowAddFolderInput(!showAddFolderInput);
    };

    const addNewScenarioHandler = (state) => {
        setIsAddNewScenario(state);
    };

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    
    const handleClose = () => {
        setAnchorEl(null);
    };

    const startScenarioModalState = () => {
        setStartScenarioModalState(!start_scenario_modal_state);
    };

    const onAddForm = (form_type) => {
        setAnchorEl(null);
        if (form_type !== "moms") {
            setMenu(menu.filter(val => val !== form_type));
        } 
        
        if (form_type === "rainfall") {
            const data = {
                data: defaultValues("rainfall")
            };
            added_form.push(
                <Fragment>
                    <Typography variant="subtitle1" display="block">
                        {capitalizeFirstLetter("Rainfall")}
                    </Typography> 
                    <RainfallForm 
                        data={data} 
                        sites={sites} 
                        sourceIds={source_ids}
                        rainfallGauges={rainfall_gauges}
                        rainfallPriorities={rainfall_priorities}
                        data_index={null}
                        selectedGroupIndexes={selected_group_indexes}
                        setSelectedGroupIndexes={setSelectedGroupIndexes}
                        setSelectedScenarioData={setSelectedScenarioData}
                        scenarioGroups={scenario_groups}
                        setScenarioGroups={setScenarioGroups}
                    />
                </Fragment>
            );
            setAddedForm(added_form);
        } else if (form_type === "subsurface") {
            const data = {
                data: defaultValues("subsurface")
            };
            added_form.push(
                <Fragment>
                    <Typography variant="subtitle1" display="block">
                        {capitalizeFirstLetter("Subsurface")}
                    </Typography> 
                    <SubsurfaceForm 
                        data={data} 
                        sites={sites}
                        sourceIds={source_ids}
                        data_index={null}
                        deployedNode={deployed_node_data}
                        tsmSensors={tsm_sensors_data}
                        selectedGroupIndexes={selected_group_indexes}
                        setSelectedGroupIndexes={setSelectedGroupIndexes}
                        setSelectedScenarioData={setSelectedScenarioData}
                        scenarioGroups={scenario_groups}
                        setScenarioGroups={setScenarioGroups}
                    />
                </Fragment>
            );
            setAddedForm(added_form);
        } else if (form_type === "surficial") {
            const data = {
                data: defaultValues("surficial")
            };

            added_form.push(
                <Fragment>
                    <Typography variant="subtitle1" display="block">
                        {capitalizeFirstLetter("Surficial")}
                    </Typography> 
                    <SurficialForm 
                        data={data} 
                        sites={sites} 
                        sourceIds={source_ids} 
                        data_index={null}
                        selectedGroupIndexes={selected_group_indexes}
                        setSelectedGroupIndexes={setSelectedGroupIndexes}
                        setSelectedScenarioData={setSelectedScenarioData}
                        scenarioGroups={scenario_groups}
                        setScenarioGroups={setScenarioGroups}/>
                </Fragment>
            );
        } else if (form_type === "earthquake") {
            const data = {
                data: defaultValues("earthquake")
            };

            added_form.push(
                <Fragment>
                    <Typography variant="subtitle1" display="block">
                        {capitalizeFirstLetter("Earthquake")}
                    </Typography> 
                    <EarthquakeForm 
                        data={data} 
                        sites={sites} 
                        sourceIds={source_ids} 
                        data_index={null}
                        selectedGroupIndexes={selected_group_indexes}
                        setSelectedGroupIndexes={setSelectedGroupIndexes}
                        setSelectedScenarioData={setSelectedScenarioData}
                        scenarioGroups={scenario_groups}
                        setScenarioGroups={setScenarioGroups}
                    />
                </Fragment>
            );
        } else if (form_type === "on_demand") {
            const data = {
                data: defaultValues("on_demand")
            };

            added_form.push(
                <Fragment>
                    <Typography variant="subtitle1" display="block">
                        {capitalizeFirstLetter("On demand")}
                    </Typography> 
                    <OnDemandForm 
                        data={data} 
                        sites={sites} 
                        sourceIds={source_ids} 
                        data_index={null}
                        selectedGroupIndexes={selected_group_indexes}
                        setSelectedGroupIndexes={setSelectedGroupIndexes}
                        setSelectedScenarioData={setSelectedScenarioData}
                        scenarioGroups={scenario_groups}
                        setScenarioGroups={setScenarioGroups}
                    />
                </Fragment>
            );
        } else if (form_type === "moms") {
            const site = {
                data: { site_code: "agb" }
            };
            added_form.push(
                <Fragment>
                    <MomsForm 
                        data={null} 
                        sites={sites}
                        site={site}
                        sourceIds={source_ids} 
                        data_index={null}
                        selectedGroupIndexes={selected_group_indexes}
                        setSelectedGroupIndexes={setSelectedGroupIndexes}
                        setSelectedScenarioData={setSelectedScenarioData}
                        scenarioGroups={scenario_groups}
                        setScenarioGroups={setScenarioGroups}
                    />
                </Fragment>
            );
        }
        
        
        
    };

    const saveNewFolder = () => {
        scenario_groups.push({
            group_name: folder_name,
            data: []
        });
        updateScenarioGroupData(scenario_groups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
                getScenarioGroupsData();
                setFolderName("");
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    const saveNewScenario = () => {
        const updated_scenario_group = scenario_groups[selected_group].data;
        updated_scenario_group.push({
            scenario_name,
            scenario_data: []
        });
        updateScenarioGroupData(scenario_groups, data => {
            const { status, message } = data;
            let variant;
            if (status) {
                variant = "success";
                getScenarioGroupsData();
                setScenarioName("");
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message,
                {
                    variant,
                    autoHideDuration: 5000,
                    action: snackBarActionFn
                }
            );
        });
    };

    const getScenarioGroupsData = () => {
        getScenarioGroups(data => {
            setScenarioGroups(data);
        });
    };

    useEffect(() => {
        getScenarioGroupsData();
        

        getAllSensorsData(data => {
            const { 
                deployed_node, rainfall_gauges: rain_gauges,
                rainfall_priorities: rain_priorities, tsm_sensors
            } = data;
            setDeployedNodeData(deployed_node);
            setRainfallGaugesData(rain_gauges);
            setRainfallPrioritiesData(rain_priorities);
            setTsmSensorsData(tsm_sensors);
        });
    }, []);

    useEffect(() => {
        setSaveFolderButtonState(true);
        setSaveNewScenarioButtonState(true);
        
        if (folder_name.length > 0) {
            setSaveFolderButtonState(false);
        }

        if (scenario_name.length > 0) {
            setSaveNewScenarioButtonState(false);
        }
    }, [folder_name, scenario_name]);

    return (
        <Fragment>
            <div className={classes.pageContentMargin}>
                <PageTitle
                    title="Operations | Usability Testing: Scenario Builder"
                    className={classes.sticky}
                />
                <Grid container spacing={3} justify="space-evenly">
                    <Grid item xs={4}>
                        <Card>
                            <CardContent component={Grid} container spacing={2}>
                                <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                    <strong>Scenario Group</strong>
                                    <Button
                                        style={{ marginLeft: "20px" }}
                                        variant="contained"
                                        color="primary"
                                        size="small"
                                        className={classes.button}
                                        onClick={showAddFolderInputField}
                                        startIcon={<Add />}
                                    >
                                        Add new folder
                                    </Button>
                                </Typography>
                                {
                                    showAddFolderInput && (
                                        <div style={{ marginBottom: "20px" }}>
                                            <TextField
                                                required
                                                label="Folder Name"
                                                value={folder_name}
                                                onChange={e => setFolderName(e.target.value)}
                                            />
                                            <Button
                                                style={{ marginTop: "20px" }}
                                                color="primary"
                                                size="small"
                                                className={classes.button}
                                                onClick={saveNewFolder}
                                                disabled={save_folder_button_state}
                                            >
                                                Save
                                            </Button>
                                            <Divider />
                                        </div>
                                    )
                                }

                                {
                                    sites.length > 0 && scenario_groups.length > 0 ? (
                                        scenario_groups.map((group, group_index) => {
                                            const { group_name, data } = group;
                                            // const { data } = group_name;

                                            return (
                                                <Accordion 
                                                    key={`${group_name}_content`}
                                                    expanded={expanded === `${group_name}`} 
                                                    onChange={handleChange(`${group_name}`, group_index)} 
                                                    className={classes.listMaxWidth}>

                                                    <AccordionSummary
                                                        expandIcon={<ExpandMore />}
                                                        aria-controls={`${group_name}_content`}
                                                        id={`${group_name}_header`}
                                                    >
                                                        <Folder color="action"/>
                                                        <Typography className={classes.heading}>
                                                            {group_name}
                                                        </Typography>
                                                    </AccordionSummary>
                                                    <AccordionDetails>
                                                        <List 
                                                            component="nav" 
                                                            aria-label="secondary mailbox folder" 
                                                            className={classes.listMaxWidth} >
                                                            <Button
                                                                style={{ marginBottom: "20px" }}
                                                                variant="contained"
                                                                color="primary"
                                                                size="small"
                                                                className={classes.button}
                                                                onClick={e => addNewScenarioHandler(true)}
                                                                startIcon={<Add />}>
                                                                Add new scenario
                                                            </Button>
                                                            {
                                                                data.length > 0 ? (
                                                                    data.map((row, group_data_index) => {
                                                                        const { scenario_name: name } = row;
                                                                        const random_key = `${Math.floor(Math.random() * 999)}${Math.floor(Math.random() * 999)}`;
                                                                        
                                                                        return (
                                                                            <ListItem
                                                                                // eslint-disable-next-line react/no-array-index-key
                                                                                key={`${group_index}_${random_key}`}
                                                                                button
                                                                                selected={selectedIndex === `${group_index}_${group_data_index}`}
                                                                                onClick={(event) => onScenarioClick(group_index, group_data_index, row)}
                                                                            >
                                                                                <ListItemText primary={`${name}`} />
                                                                            </ListItem>
                                                                        );
                                                                    })


                                                                ) : (
                                                                    <Typography variant="body1" gutterBottom>
                                                                        No data.
                                                                    </Typography>
                                                                )
                                                            }
                                                        </List>
                                                    </AccordionDetails>
                                                </Accordion>
                                            );
                                        })
                                    ) : (
                                        <Typography variant="body1" gutterBottom>
                                            No data.
                                        </Typography>
                                    )
                                }
                            </CardContent>
                        </Card>
                    </Grid>

                    <Grid item xs={8}>
                        <Card>
                            <CardContent component={Grid} container spacing={2}>
                                {
                                    // eslint-disable-next-line no-nested-ternary
                                    isAddNewScenario ? (
                                        <div style={{ marginBottom: "20px" }}>
                                            <TextField
                                                required
                                                label="New scenario name"
                                                value={scenario_name}
                                                onChange={e => setScenarioName(e.target.value)}
                                            />
                                            <Button
                                                style={{ marginTop: "20px" }}
                                                color="primary"
                                                size="small"
                                                className={classes.button}
                                                onClick={saveNewScenario}
                                                disabled={save_new_scenario_button_state}
                                            >
                                                Save
                                            </Button>
                                            <Divider />
                                        </div>
                                    ) : (
                                        
                                        selected_scenario ? (
                                            <Fragment>
                                                <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>
                                                    Build scenario for <strong>{selected_scenario}</strong> 
                                                    <Button
                                                        onClick={handleClick}
                                                        aria-controls="simple-menu" aria-haspopup="true"
                                                        style={{ marginLeft: "20px" }}
                                                        variant="contained"
                                                        color="primary"
                                                        size="small"
                                                        className={classes.button}
                                                        startIcon={<Add />}
                                                    >
                                                        Add Alert
                                                    </Button>
                                                    <Menu
                                                        style={{ marginTop: "50px" }}
                                                        id="form-menu"
                                                        anchorEl={anchorEl}
                                                        keepMounted
                                                        open={Boolean(anchorEl)}
                                                        onClose={handleClose}
                                                    >
                                                        {
                                                            menu && (
                                                                menu.map(menu_row => {
                                                                    return (
                                                                        <MenuItem onClick={e => onAddForm(menu_row)} key={menu_row}>{capitalizeFirstLetter(menu_row.replace("_", " "))}</MenuItem>
                                                                    );
                                                                })
                                                            )
                                                        }
                                                    </Menu>
                                                    <Button
                                                        style={{ marginLeft: "20px" }}
                                                        variant="contained"
                                                        color="primary"
                                                        size="small"
                                                        className={classes.button}
                                                        onClick={startScenarioModalState}
                                                        startIcon={<PlayCircleFilled />}
                                                    >
                                                        Start scenario
                                                    </Button>
                                                </Typography>
                                                <ScenarioList 
                                                    selectedScenarioData={selected_scenario_data}
                                                    setSelectedScenarioData={setSelectedScenarioData}
                                                    sourceIds={source_ids}
                                                    deployedNode={deployed_node_data}
                                                    rainfallGauges={rainfall_gauges}
                                                    rainfallPriorities={rainfall_priorities}
                                                    tsmSensors={tsm_sensors_data}
                                                    selectedGroupIndexes={selected_group_indexes}
                                                    setSelectedGroupIndexes={setSelectedGroupIndexes}
                                                    scenarioGroups={scenario_groups}
                                                    setScenarioGroups={setScenarioGroups}
                                                    addedForm={added_form}
                                                    onScenarioClick={onScenarioClick}
                                                />
                                            </Fragment>
                                        ) : (
                                            <Typography variant="subtitle1" display="block" component={Grid} item xs={12}>No selected scenario.</Typography>
                                        )
                                        
                                        
                                    )
                                }
                                <Grid container>
                                    {
                                        added_form.length > 0 && (
                                            <Fragment>
                                                <div style={{ textAlign: "center", width: "100%" }}>
                                                    <Typography variant="subtitle1">
                                                        New Alerts
                                                    </Typography>
                                                    <hr/>
                                                </div>
                                            </Fragment>
                                        )
                                    }
                                    {
                                        added_form.map((row, index) => {
                                            return (
                                                <Grid item xs={12} key={`added_scenario_data_list_${index}`} style={{ paddingTop: "5px" }}>
                                                    <Card className={classes.listMaxWidth}>
                                                        <CardContent>
                                                            {row}
                                                        </CardContent>
                                                    </Card>
                                                </Grid>
                                            );
                                        })
                                    }
                                </Grid>
                                
                            </CardContent>
                            <CardActions style={{ justifyContent: "flex-end" }}>
                                {/* <Button color="primary">Save</Button> */}
                            </CardActions>
                        </Card>
                    </Grid>
                </Grid>
            </div>
            <StartScenarioModal
                modalState={start_scenario_modal_state}
                modalStateHandler={startScenarioModalState}
                scenarioName={selected_scenario}
                scenarioData={selected_scenario_data}
                deployedNodeData={deployed_node_data}
                tsmSensorsData={tsm_sensors_data}
                rainfallGauges={rainfall_gauges}
                sites={sites}
            />
        </Fragment>
    );
}

export default Container;